@import 'mixins';

.tools
{

  width: 90vw;
  max-width: 1200px;

  background-color: #999;
  // 0.3rem -> 5px
  padding: calc(20px * var(--scale));
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;

  @include flex();
  gap: calc(15px * var(--scale));

  @media screen and (max-width: 1024px) {
    flex-direction: column;
  }

  > *
  {
    @include flex();
    gap: calc(15px * var(--scale));
  }

  .brush-size
  {
    position: relative;
    > button
    {
      @include generic-text-button();
    }
    .list
    {
      @include setColor(#333, whitesmoke);
      position: absolute;
      top: calc(100% +  calc(5px * var(--scale)));
      left: 0px;
      z-index: 2;
      padding: calc(10px * var(--scale));
      border-radius: calc(5px * var(--scale));
      border: 1px solid black;
      @include flex(column);
      gap: calc(10px * var(--scale));
      list-style: none;
      li
      {
        .item
        {
          padding: calc(5px * var(--scale));
          border-radius: calc(5px * var(--scale));
          @include flex();
          gap: calc(10px * var(--scale));
          .line
          {
            width: calc(100px * var(--scale)); 
            background-color: #333;
          }
        }
        .item.selected
        {
          @include setColor(whitesmoke, #333);
          .line
          {
            background-color: whitesmoke;
          }
        }
      }
    }
  }
  .undo,
  .redo,
  .brush,
  .bucket
  {
    @include generic-icon-button();
  }
  .brush.selected,
  .bucket.selected
  {
    @include setColor(#333, whitesmoke)
  }

  .colors
  {
    @include grid(10, 3);
    gap: calc(5px * var(--scale));

    .color
    {
      width: calc(20px * var(--scale));
      height: calc(20px * var(--scale));
      border-radius: 50%;
    }
    .color.selected
    {
      border: calc(2px * var(--scale)) solid white;
    }
  }
  .color-input-container
  {
    @include flex(column, center, flex-start);
    gap: calc(5px * var(--scale));
    label
    {
      font-size: 1rem;
    }
    #color-input
    {
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      width: calc(50px * var(--scale));
      height: calc(50px * var(--scale));
      background-color: transparent;
      border: none;
      cursor: pointer;
    }
    #color-input::-webkit-color-swatch
    {
      border-radius: calc(10px * var(--scale));
      border: none;
    }
    #color-input::-moz-color-swatch
    {
      border-radius: calc(10px * var(--scale));
      border: none;
    }
  }
  .download
  {
    text-decoration: none;
    @include generic-text-button();
  }
}